import React from 'react';
import PayBtn from '../PayBtn/payBtn';
import Modal from "../Modal/modal";
import { MDBJumbotron, MDBCardBody, MDBCardText, MDBCardTitle, MDBInput } from "mdbreact";

const Jumbotron = () => {
  const checkBox = React.createRef();

  return (
    <>
      <section>
        <MDBJumbotron className="text-center mx-auto p-3 my-5">
          <MDBCardBody>
            <MDBCardTitle>Регистрации и&nbsp;оплата участия</MDBCardTitle>
            <p className="default-color-text my-4 font-weight-bold">Участие в&nbsp;Олимпиаде&nbsp;&mdash; добровольное и&nbsp;платное</p>
            <p>Стоимость участия в Олимпиаде - 3 бел. рубля</p>
            <MDBCardText>
              Для участия в&nbsp;Олимпиаде необходимо ознакомится с у&nbsp;Инструкцией
          </MDBCardText>
            <hr className="my-4" />
            <div className="pt-2">
              <Modal />
              <PayBtn checkBox={checkBox} />
            </div>
            <div className="custom-input-checkbox mt-3">
              <MDBInput label="С условиями проведения Олимпиады согласен" filled type="checkbox" id="checkbox2" ref={checkBox} />
            </div>
          </MDBCardBody>
        </MDBJumbotron>
      </section>
    </>
  )
}

export default Jumbotron